import { useState, useEffect, useRef } from "react";
import React from "react";

const Content = () => {
  const redirect = (url) => {
    window.location.href = url;
  };

  const products = [
    {
      title: "Ipad Pro",
      id: "product#1",
      description:
        "Now with a 12.2in screen. 500GB of storage and new sound system.",
      image:
        "https://res.cloudinary.com/ddhcwhh5n/image/upload/v1663945321/ipad_t2obrp.png",
      url: "./error404"
    },
    {
      title: "Iphone 14",
      id: "product#2",
      description: "Now with 1TB of storage, and even more OVERPRICED!",
      image:
        "https://res.cloudinary.com/ddhcwhh5n/image/upload/v1663945492/New_Project_3_swfpg2.png",
      url: "./error404"
    },
    {
      title: "AirPods (3rd generation)",
      id: "product#3",
      description:
        "Now with new design, low frequencies enhanced and new tracking system.",
      image:
        "https://res.cloudinary.com/ddhcwhh5n/image/upload/v1663945248/New_Project_2_kch5my.png",
      url: "./error404"
    }
  ];

  const [imgWidth, setImgWidth] = useState(0);
  const img = useRef(null);

  const checkSize = () => {
    setImgWidth(img.current.offsetHeight * 1.33333333);
  };

  useEffect(() => {
    setImgWidth(img.current.offsetHeight * 1.33333333);
    window.addEventListener("resize", checkSize);
    return () => {
      window.removeEventListener("resize", checkSize);
    };
  }, []);

  return (
    <section className="content">
      <section className="product">
        <article className="productArticleContainer">
          {products.map((item, i) => {
            return (
              <div
                key={item.id}
                className="productElement"
                style={{ backgroundColor: i % 2 !== 0 ? "black" : "white" }}
              >
                <h2 style={{ color: i % 2 === 0 ? "black" : "white" }}>
                  {item.title}
                </h2>
                <p style={{ color: i % 2 === 0 ? "black" : "white" }}>
                  {item.description}
                </p>
                <button className="btn" onClick={() => redirect(item.url)}>
                  Buy Now!
                </button>
                <div
                  className="productImgContainer"
                  style={{ width: imgWidth }}
                >
                  <img ref={img} src={item.image} alt={item.id} />
                </div>
              </div>
            );
          })}
        </article>
      </section>
    </section>
  );
};

export default Content;
