import React, { useRef, useEffect, useState } from "react";
import Logo from "./resources/Logo";

const Hero = () => {
  const video = useRef(null);
  const img = useRef(0);
  const [imgSize, setImgSize] = useState(0);
  const [videoHeight, setVideoHeight] = useState(0);
  const [size, setSize] = useState({
    height: window.innerHeight,
    width: window.innerWidth
  });

  useEffect(() => {
    setImgSize({
      imgHeight: img.current.offsetHeight,
      imgWidth: img.current.offsetWidth
    });
    video.current.play();
  }, [img.current.offsetHeight, img.current.offsetWidth, video]);

  const checkSize = () => {
    setSize({ height: window.innerHeight, width: window.innerWidth });
  };

  useEffect(() => {
    window.addEventListener("resize", checkSize);
    return () => {
      window.removeEventListener("resize", checkSize);
    };
  }, []);

  useEffect(() => {
    setVideoHeight(video.current.offsetHeight);
  }, [size]);

  const redirect = (url) => {
    window.location.href = url;
  };

  const videoSrc = [
    "https://res.cloudinary.com/ddhcwhh5n/video/upload/v1663296297/final_3_eose0p.mp4",
    "https://res.cloudinary.com/ddhcwhh5n/video/upload/v1663729140/JM/rotate_fist_1_1_ukunmk.mp4"
  ];

  return (
    <section
      className="hero"
      // size.width * 0.5625
    >
      <div className="heroImg" ref={img}>
        <video ref={video} muted loop>
          <source
            type="video/mp4"
            src={videoSrc[Math.floor(Math.random() * 2)]}
            // src="https://res.cloudinary.com/ddhcwhh5n/video/upload/v1663296297/final_3_eose0p.mp4"
            // src="https://res.cloudinary.com/ddhcwhh5n/video/upload/v1663729140/JM/rotate_fist_1_1_ukunmk.mp4"
          />
        </video>
        <div
          className="heroContent"
          style={{
            height: videoHeight,
            marginTop: videoHeight * -1
          }}
        >
          <Logo fill="white" height="25%" />
          <h2>The music of your heart is now on your wrist.</h2>
          <button className="btn" onClick={() => redirect("./error404")}>
            Buy Now!
          </button>
        </div>
      </div>
    </section>
  );
};

export default Hero;
